import firebase from 'firebase';
import { stopSubmit } from 'redux-form';

const SET_USER_DATA = 'auth/SET_USER_DATA'


let initialState = {
    uid: null,
    name: null,
    email: null,
    photoUrl: null,
    emailVerified: null,
    isAuth: false,
}
const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        default:
            return state;
    }
}
export const setAuthUserData = (uid, name, email, photoUrl, emailVerified, isAuth) => ({
    type: SET_USER_DATA, payload: { uid, name, email, photoUrl, emailVerified, isAuth }
})
export const signIn = (email, password) => (dispatch) => {
    firebase.auth().signInWithEmailAndPassword(email, password)
        .then(userCredential => {
            dispatch(setAuthUserData(
                userCredential.user.uid,
                userCredential.user.displayName,
                userCredential.user.email,
                userCredential.user.photoURL,
                userCredential.user.emailVerified,
                true))
            console.log('signInSuccess =>', userCredential.user)
        })
        .catch(error => {
            dispatch(stopSubmit("signIn", { _error: error.message }))
            console.error('signInError =>', error.code)
        })
}
export const signUp = (firstName, email, password) => (dispatch) => {
    firebase.auth().createUserWithEmailAndPassword(email, password)
        .then(userCredential => {
            dispatch(setAuthUserData(
                userCredential.user.uid,
                userCredential.user.displayName,
                userCredential.user.email,
                userCredential.user.photoURL,
                userCredential.user.emailVerified,
                true))
            firebase.firestore().collection("users").doc(userCredential.user.uid).set({
                // uid: userCredential.user.uid,
                firstName,
                email: userCredential.user.email,
                photoURL: userCredential.user.photoURL,
                orders: []
            })
            console.log('signUpSuccess =>', userCredential.user)
        })
        .catch(error => {
            dispatch(stopSubmit("signUp", { _error: error.message }))
            console.error('signUpError =>', error.code)
        })
}
export const signOut = () => (dispatch) => {
    firebase.auth().signOut()
        .then(() => {
            console.log('signOutSuccess ');
            dispatch(setAuthUserData(null, null, null, null, null, false))
        })
        .catch(error => {
            console.error('signOutError ', error)
        })
};
export const getAuthUserData = () => (dispatch) => {
    firebase.auth().onAuthStateChanged((user) => {
        if (user) {
            console.log('User is signed in', user.uid, user.displayName, user.email, user.photoURL, user.emailVerified, true);
            // dispatch(setAuthUserData(user.uid, user.displayName, user.email, user.photoURL, user.emailVerified, true))
        } else {
            console.log('No user is signed in')
        }
    })
}
export const updateUserName = (displayName) => {
    let user = firebase.auth().currentUser;
    console.log(user.uid)
    user.updateProfile({ displayName })
        .then(() => {
            firebase.firestore().collection("users").doc(user.uid).update({ firstName: displayName })
            console.log('Update profile name success')
        })
        .catch((error) => { console.error('Update profile name error', error) });
}
export const updateUserPhoto = (photoURL) => {
    let user = firebase.auth().currentUser;
    user.updateProfile({ photoURL })
        .then(() => {
            firebase.firestore().collection("users").doc(user.uid).update({ photoURL })
            console.log('Update profile photo success!')
        })
        .catch((error) => { console.error('Update profile photo error!', error) });
}
export default authReducer