import React, { useEffect, useState } from 'react';
import { Navbar, Nav, } from 'react-bootstrap';
import { NavLink, } from "react-router-dom";
import { getAuthUserData, signOut } from '../../Redux/auth-reducer';
import { connect } from 'react-redux';
import { Image } from 'react-bootstrap'
import ava from '../../Assets/img/ava.gif'
import style from './Header.module.css'
import Basket from '../Basket/Basket';
import basketCard from '../../Assets/img/basket-cart-main.png'
import firebase from 'firebase';

const Header = (props) => {
  const [loading, setLoading] = useState(true)
  const [displayName, setDisplayName] = useState('')
  const [photoURL, setPhotoURL] = useState('')
  const [orders, setOrsers] = useState([])
  useEffect(() => {
    if (props.uid) {
      firebase.firestore().collection("users").doc(props.uid)
        .onSnapshot((doc) => {
          const obj = { ...doc.data() }
          setDisplayName(obj.firstName)
          setPhotoURL(obj.photoURL)
          setOrsers(obj.orders)
          console.log(obj.orders)
          setLoading(false)
        });
    }
  }, [props.uid])

  const signOut = () => {
    props.signOut()
  }
  const [modalShow, setModalShow] = useState(false);


  // console.log('orders', orders)

  return (
    <Navbar bg="dark" variant="dark" fixed="top" expand="sm">
      <Navbar.Brand >My APP</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <NavLink className="nav-link" to={"/home"}>Home</NavLink>
          <NavLink className="nav-link" to={"/shop"}>Shop</NavLink>
          <NavLink className="nav-link" to={"/admin"}>Admin</NavLink>
        </Nav>
        <Nav>
          <a onClick={() => setModalShow(true)} className={style.basket}>
            <img src={basketCard} style={{ height: '35px', margin: '5px' }} />
            {orders.length > 0 && <b className={style.basketItems}>{orders.length}</b>}
          </a>
          <Basket
            orders={orders}
            show={modalShow}
            onHide={() => setModalShow(false)}
          />
          {props.uid
            ? <>
              <NavLink className="nav-link" to={"/profile"}>{displayName}
                <Image src={photoURL ? photoURL : ava} roundedCircle className={style.ava} />
              </NavLink>
              <NavLink className="nav-link" to="/" onClick={signOut}>Sign Out</NavLink>
            </>
            : <>
              <NavLink className="nav-link" to={"/sign-in"}>Sign in</NavLink>
              <NavLink className="nav-link" to={"/sign-up"}>Sign up</NavLink>
            </>
          }
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}
const mapStateToProps = (state) => ({
  uid: state.firebase.auth.uid,
})

export default connect(mapStateToProps, { signOut, getAuthUserData })(Header) 