import React, { useEffect, useState } from 'react';
import { Accordion, Card, ListGroupItem, ListGroup, Dropdown } from 'react-bootstrap'
import style from './Shop.module.css'
import firebase from 'firebase'
import Preloader from '../../Components/Preloader/Preloader';
import { filterByMinMax, filterByObjKey } from '../../Shared/Filters';
import { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';
import { connect } from 'react-redux';

const Shop = (props) => {
    const [loading, setLoading] = useState(true);
    // Menu buttons
    const [brendArr, setBrendArr] = useState([])
    useEffect(() => {
        firebase.firestore().collection("productCategory").doc("brendName")
            .onSnapshot((doc) => {
                const arr = []
                for (let index = 0; index < doc.data().brendName.length; index++) {
                    const element = doc.data().brendName[index];
                    arr.push(element)
                }
                setBrendArr(arr)
            });
    }, [])
    const [typeArr, setTypeArr] = useState([])
    useEffect(() => {
        firebase.firestore().collection("productCategory").doc("type")
            .onSnapshot((doc) => {
                const arr = []
                for (let index = 0; index < doc.data().type.length; index++) {
                    const element = doc.data().type[index];
                    arr.push(element)
                }
                setTypeArr(arr)
            });
    }, [])
    // get product end sort product
    const [products, setProducts] = useState([])
    const [sortTarget, setSortTarget] = useState(null)
    const [sortName, setSortName] = useState('Сортувати за...')

    const sortByPrice = (e) => {
        setSortName(e.target.textContent)
        if (e.target.id === 'priceUp') { setSortTarget("asc") }
        if (e.target.id === 'priceDn') { setSortTarget("desc") }
    }
    const sortByNew = (e) => {
        setSortName(e.target.textContent)
        if (e.target.id === 'new') { setSortTarget("new") }
    }

    useEffect(() => {
        if ((sortTarget === 'asc') || (sortTarget === 'desc')) {
            return firebase.firestore().collection("product")
                .orderBy("price", sortTarget)
                .onSnapshot((snapshot) => {
                    const productsData = [];
                    snapshot.forEach(doc => productsData.push({ ...doc.data(), id: doc.id }));
                    setProducts(productsData)
                    setLoading(false)
                })
        } else if (sortTarget === 'new') {
            return firebase.firestore().collection("product")
                .orderBy("dateAdd", 'desc')
                .onSnapshot((snapshot) => {
                    const productsData = [];
                    snapshot.forEach(doc => productsData.push({ ...doc.data(), id: doc.id }));
                    setProducts(productsData)
                    setLoading(false)
                })
        } else {
            return firebase.firestore().collection("product")
                .onSnapshot((snapshot) => {
                    const productsData = [];
                    snapshot.forEach(doc => productsData.push({ ...doc.data(), id: doc.id }));
                    setProducts(productsData)
                    setLoading(false)
                })
        }

    }, [sortTarget])

    // filters
    const [maxPrise, setMaxPrise] = useState(0)
    const [valuePrice, setValuePrice] = useState([])
    useEffect(() => {
        firebase.firestore().collection("product")
            .onSnapshot((snapshot) => {
                const arr = []
                let max = 0
                snapshot.forEach(doc => arr.push(doc.data()));
                for (let i = 0; i < arr.length; i++) {
                    if (arr[i].price > max) max = arr[i].price
                }
                setMaxPrise(max)
                console.log(maxPrise)
            });
        setValuePrice([0, maxPrise])
    }, [maxPrise])
    const changePriceInterval = (e) => {
        setValuePrice(e);
        console.log(e);
    };

    const [filterNameArr, setFilterNameArr] = useState([])
    const [filterTypeArr, setFilterTypeArr] = useState([])
    const addFilterName = (e) => {
        if (e.target.checked === true) {
            setFilterNameArr((prev) => [...prev, e.target.name])
        }
        if (e.target.checked === false) {
            setFilterNameArr(filterNameArr.filter(arr => arr !== e.target.name))
        }
    }
    const addFilterType = (e) => {
        if (e.target.checked === true) {
            setFilterTypeArr((prev) => [...prev, e.target.name])
        }
        if (e.target.checked === false) {
            setFilterTypeArr(filterTypeArr.filter(arr => arr !== e.target.name))
        }
    }

    const buy = async (e) => {
        console.log(e.target.name)

        const resp = await firebase.firestore().collection("product").doc(e.target.name).get()
        const washingtonRef = firebase.firestore().collection("users").doc(props.uid)
        washingtonRef.update({
            orders: firebase.firestore.FieldValue.arrayUnion(resp.id)
        });
        // washingtonRef.update({
        //     orders: firebase.firestore.FieldValue.arrayRemove(resp.id)
        // });
    }

    return (
        <>
            <div className={style.container}>
                <div className={style.menu}>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0" >
                                Brend
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                    {
                                        brendArr && brendArr.map(p => {
                                            return (
                                                <div>
                                                    <label>
                                                        <input name={p} onChange={addFilterName} className="m-2" type='checkbox' />
                                                        <span className="m-2">{p}</span>
                                                    </label>
                                                </div>
                                            )
                                        })
                                    }
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                Size Wheel
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                    <Range value={valuePrice} onChange={changePriceInterval} max={maxPrise} />
                                    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                        <span>{valuePrice[0]}</span>
                                        <span>{valuePrice[1]}</span>
                                    </div>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                Type
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                    {
                                        typeArr && typeArr.map(p => {
                                            return (
                                                <div>
                                                    <label>
                                                        <input name={p} onChange={addFilterType} className="m-2" type='checkbox' />
                                                        <span className="m-2">{p}</span>
                                                    </label>
                                                </div>
                                            )
                                        })
                                    }
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                Size Wheel
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                    size wheel body
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                Size Frame
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>Hello! I'm the body</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                Frame Material
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>Hello! I'm the body</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                </div>
                <div className={style.body}>
                    <div style={{ textAlign: 'end', padding: '5px 5%', background: 'white' }}>
                        <Dropdown>
                            <Dropdown.Toggle variant="secondary" id="dropdown-basic">
                                {sortName}
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <Dropdown.Item onClick={sortByPrice} id='priceUp'>Від дешевих до дорогих</Dropdown.Item>
                                <Dropdown.Item onClick={sortByPrice} id='priceDn'>Від дорогих до дешевих</Dropdown.Item>
                                <Dropdown.Item onClick={sortByNew} id='new'>Новинки</Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>
                    <div className={style.bodyItems}>
                        {
                            !loading
                                ? filterByObjKey(
                                    filterByObjKey(
                                        filterByMinMax(products, valuePrice[0], valuePrice[1]), filterNameArr), filterTypeArr)
                                    .map(p => {
                                        return (
                                            <Card style={{ width: '33%', margin: '.5px' }}>
                                                <Card.Img variant="top" src={p.productImage ? p.productImage : null} />
                                                <ListGroup className="list-group-flush">
                                                    <ListGroupItem>Бренд: {p.brendName}</ListGroupItem>
                                                    <ListGroupItem>Тип: {p.type}</ListGroupItem>
                                                    <ListGroupItem>Матеріал рами: {p.frameMaterial}</ListGroupItem>
                                                    <ListGroupItem>Розмір рами: {p.sizeFrame}</ListGroupItem>
                                                    <ListGroupItem>Розмір коліс: {p.sizeWheel}</ListGroupItem>
                                                    <ListGroupItem>Ціна: {p.price}</ListGroupItem>
                                                </ListGroup>
                                                <Card.Body>
                                                    <Card.Title>Card Title</Card.Title>
                                                    <Card.Text>
                                                        Some quick example text to build on the card title and make up the bulk of
                                                        the card's content.
                                                        </Card.Text>
                                                </Card.Body>
                                                <Card.Body>
                                                    <Card.Link className="ml-2">Детальніше про товар</Card.Link>
                                                    <input className="float-right btn btn-primary mt-1" onClick={buy} name={p.id} type="button" value="Купити" />

                                                </Card.Body>
                                            </Card>
                                        )
                                    })
                                : <Preloader />
                        }
                    </div>
                </div>

            </div>
        </>
    )
}
const mapStateToProps = (state) => ({
    uid: state.firebase.auth.uid,
})
export default connect(mapStateToProps,)(Shop)