import React, { useEffect, useState } from "react";
import { Modal, Button, } from "react-bootstrap";
import firebase from 'firebase'
import { connect } from 'react-redux';
import style from './Basket.module.css'
import delItemImg from '../../Assets/img/basketCartDel.png'
const Basket = (props) => {
    const [items, setItems] = useState([])
    const [allSum, setAllSum] = useState()

    useEffect(async () => {
        const arr = []
        let s = 0
        for (let i = 0; i < props.orders.length; i++) {
            const resp = await firebase.firestore().collection("product").doc(props.orders[i]).get()
            arr.push({ ...resp.data(), id: i + 1, uid: props.orders[i] })
            s += resp.data().price
        }
        setItems(arr)
        setAllSum(s)
        console.log(arr)
    }, [props.orders])
    const delPr = async (e) => {
        console.log(e.target.name)

        const resp = await firebase.firestore().collection("product").doc(e.target.name).get()
        const washingtonRef = firebase.firestore().collection("users").doc(props.uid)
        washingtonRef.update({
            orders: firebase.firestore.FieldValue.arrayRemove(resp.id)
        });
    }
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Basket
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div>
                    {items.length === 0 ? <h4>Кошик порожній</h4> :
                        items.map((p) => {
                            return (
                                <div>
                                    <div className={style.productBody}>
                                        <h2>{p.id}</h2>
                                        <img src={p.productImage} style={{ height: '200px' }} />
                                        <h5>Велосипед {p.brendName}</h5>
                                        <button type="button" onClick={delPr} name={p.uid}
                                            style={{ margin: 'auto', height: '50px', width: '50px' }}>
                                            <img name={p.uid} src={delItemImg} alt="" style={{ margin: 'auto', height: '40px' }} />
                                            Видалити
                                        </button>

                                    </div>
                                    <p>Ціна: {p.price}</p>
                                    <div>
                                        <button>-</button>
                                        <input type="text" name="" disabled />
                                        <button>+</button>
                                        <b style={{ float: 'right' }}>Сума: {p.price}</b>
                                    </div>
                                    <hr />
                                </div>

                            )
                        })
                    }
                    <b style={{ float: 'right' }}>Загалом: {allSum}</b>

                </div>
            </Modal.Body>
            <Modal.Footer>

                <Button onClick={props.onHide}>Close</Button>
            </Modal.Footer>
        </Modal>
    )
}
const mapStateToProps = (state) => ({
    uid: state.firebase.auth.uid,
})
export default connect(mapStateToProps,)(Basket)